/**
 * axios
 */
import config from '$config'
import router from './../../router'
import { FastJsonResolver } from 'utils/FastJsonResolver'
import { Message } from 'element-ui'
/**
 * AjaxResult
 * @method handleRequest
 * @param  {[type]}      result 
 */
function handleResult (result, router) {
  if (result && result['type']) {
    switch (result['type']) {
      case 'SUCCESS':
        if (result['code']) {
          handleSuccessCode(result.code, router)
        }
        if (result['message']) {
          Message.success(result.message)
        }
        break
      case 'ERROR':
        if (result['code']) {
          handleErrorCode(result.code, router)
          Message.warning(result.message)
        }
        break
    }
  }
}
/**
 * 
 * @method handleSuccessCode
 * @param  {String}          code   [description]
 */
function handleSuccessCode (code, router) {
  switch (code) {
    case 'ACCOUNT_LOGOUT':
      router.push('/login')
      break
  }
}
/**
 * 
 * @method handleErrorCode
 * @param  {String}          code MessageCode
 * @return {[type]}               [description]
 */
function handleErrorCode (code, router) {
  switch (code) {
    case 'PAGE_NOT_FOUND':
      break
    case 'ACCOUNT_NO_LOGIN':
      router.push('/login')
      break
  }
}
export default {
  // `baseURL`  `url`  `url`  URL
  //  `baseURL`  axios  URL
  baseURL: config.baseURL,
  // `transformRequest` 
  //  'PUT', 'POST'  'PATCH' 
  //  ArrayBuffer Stream
  transformRequest: [function (data) {
    //  data 
    data = data || {}
    return JSON.stringify(data)
    // return data
  }],
  // `transformResponse`  then/catch 
  transformResponse: [function (data) {
    if (typeof data === 'object') {
      if (JSON.stringify(data).indexOf('$ref') > -1) {
        new FastJsonResolver(data).resolve()
      }
      handleResult(data, router)
    }
    console.log('')
    return JSON.parse(data)
  }],
  // `headers` 
  headers: {
    // 'Content-Type': 'application/json'
    // 'X-Authorization': ''
  },
  // `params`  URL 
  // (plain object) URLSearchParams 
  // params: {
  //   ID: 12345
  // },
  // `paramsSerializer`  `params` 
  // (e.g. https://www.npmjs.com/package/qs, http://api.jquery.com/jquery.param/)
  // paramsSerializer: function(params) {
  //   return Qs.stringify(params, {arrayFormat: 'brackets'})
  // },
  // `data` 
  //  'PUT', 'POST',  'PATCH'
  //  `transformRequest` 
  // - string, plain object, ArrayBuffer, ArrayBufferView, URLSearchParams
  // - FormData, File, Blob
  // - Node  Stream
  // data: {
  // },

  // `timeout` (0 )
  //  `timeout` 
  timeout: 0,
  // `withCredentials` 
  withCredentials: true, // 

  // `adapter` 
  //  promise  ( [response docs](#response-api)).
  // adapter: function (config) {
  //   /* ... */
  // },

  // `auth`  HTTP 
  //  `Authorization`  `headers`  `Authorization`
  // auth: {
  //   username: 'janedoe',
  //   password: 's00pers3cret'
  // },

  // `responseType`  'arraybuffer', 'blob', 'document', 'json', 'text', 'stream'
  responseType: 'json', // 

  // `xsrfCookieName`  xsrf token cookie
  xsrfCookieName: 'XSRF-TOKEN', // default

  // `xsrfHeaderName`  xsrf token  HTTP 
  xsrfHeaderName: 'X-XSRF-TOKEN', // default

  // `onUploadProgress` 
  // onUploadProgress: function (progressEvent) {
  // },

  // `onDownloadProgress` 
  // onDownloadProgress: function (progressEvent) {
  // },

  // `maxContentLength` 
  // maxContentLength: 2000,

  // `validateStatus` HTTP  resolve  reject  promise  `validateStatus`  `true` ( `null`  `undefined`)promise  resolve; promise  rejecte
  validateStatus: function (status) {
    return status >= 200 && status < 300 // 
  }
}



